.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 40%) scaleX(-1);
		@include min-md {
			width: 200px;
			transform: translate(-30%, 40%) scaleX(-1);
		}
		@include min-lg {
			width: 240px;
			transform: translate(-20%, 40%) scaleX(-1);
		}
		@include min-xl {
			width: 280px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(40%, 40%);
		@include min-md {
			width: 200px;
			transform: translate(30%, 40%);
		}
		@include min-lg {
			width: 240px;
			transform: translate(20%, 40%);
		}
		@include min-xl {
			width: 280px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, -50%) scaleX(-1);
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
			transform: translate(-50%, -40%) scaleX(-1);
		}
		@include min-xl {
			width: 260px;
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, -80%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
			transform: translate(40%, -50%);
		}
		@include min-xl {
			width: 260px;
		}
	}
	.orn-5 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 60px;
		transform: rotate(45deg);
		@include min-xs {
			width: 80px;
		}
		@include min-md {
			top: 2rem;
			left: -1rem;
			width: 100px;
		}
	}
	.orn-6 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 50px;
		transform: rotate(-60deg);
		@include min-xs {
			width: 70px;
		}
		@include min-md {
			top: 1rem;
			right: -1rem;
			width: 90px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		margin: 0 auto;
		z-index: 5;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 70%;
				height: auto;
				aspect-ratio: 3/4;
				object-fit: cover;
				border-radius: 50%;
			}
			.frame {
				position: relative;
				width: 100%;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			border-radius: var(--border-radius);
			background-color: var(--color-primary);
			padding: 10rem 2rem 3rem;
			margin-top: -8rem;
			@include max-md {
				padding: 8rem 1rem 2rem;
				margin-top: -6rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
