.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 60%;
		left: 0;
		width: 120px;
		transform: translate(-35%, -50%);
		@include min-md {
			width: 160px;
		}
		.orn-2 {
			position: relative;
			width: 120px;
			transform: translate(-35%, -10%) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 160px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 60%;
		right: 0;
		width: 120px;
		transform: translate(35%, -50%) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		.orn-4 {
			position: relative;
			width: 120px;
			transform: translate(-35%, -10%) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 160px;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
