.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			@include max-lg {
				transform: translate(-50%, -40%);
			}
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
			@include max-md {
				width: 32px;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		background-color: var(--color-primary);
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-75%, -20%) scaleX(-1) rotate(-60deg);
			@include min-xs {
				width: 180px;
			}
			@include min-md {
				width: 200px;
				transform: translate(-60%, -10%) scaleX(-1) rotate(-60deg);
			}
			@include min-lg {
				width: 240px;
			}
			@include min-xl {
				width: 260px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 2rem;
			right: 1rem;
			width: 60px;
			transform: rotate(45deg);
			@include min-xs {
				width: 80px;
			}
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				top: 5%;
				right: 8%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(50%, -20%) scaleX(-1);
			@include min-md {
				width: 200px;
				transform: translate(50%, -50%) scaleX(-1);
			}
			@include min-lg {
				width: 240px;
			}
			@include min-xl {
				width: 260px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 40%;
			left: 0;
			width: 180px;
			transform: translate(-50%, 50%);
			@include min-md {
				bottom: 10%;
				width: 200px;
			}
			@include min-lg {
				width: 240px;
			}
			@include min-xl {
				width: 260px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: rotate(-20deg);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 0;
			width: 60px;
			transform: rotate(-65deg);
			@include min-xs {
				width: 80px;
			}
			@include min-md {
				width: 100px;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 50px;
			transform: rotate(45deg);
			@include min-xs {
				width: 70px;
			}
			@include min-md {
				width: 90px;
			}
		}
		.orn-9 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 180px;
			transform: translate(-20%, -50%) rotate(20deg) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-10 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 180px;
			transform: translate(20%, -50%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-11 {
			position: absolute;
			top: 2rem;
			right: 0;
			width: 50px;
			transform: rotate(-65deg);
			@include min-xs {
				width: 70px;
			}
			@include min-md {
				width: 90px;
			}
		}
		.orn-12 {
			position: absolute;
			bottom: 1rem;
			left: 0;
			width: 60px;
			transform: rotate(45deg);
			@include min-xs {
				width: 80px;
			}
			@include min-md {
				width: 100px;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		&.man {
			text-align: left;
		}
		&.woman {
			text-align: right;
		}
		.image-wrap {
			position: relative;
			width: 90%;
			margin: 0 auto;
			@include max-md {
				width: 100%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.frame {
					position: relative;
					width: 100%;
					height: auto;
				}
				.couple-image {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					aspect-ratio: 7/8;
					width: 70%;
					height: auto;
					object-fit: cover;
					border-radius: 50%;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-light);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-secondary);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
