.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-light);
		}
	}
	.orn-99 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 150%;
		transform: translate(-50%, -50%);
		@include max-lg {
			width: 140%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 70%;
		transform: translate(-40%, -20%);
		@include min-lg {
			width: 50%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 70%;
		transform: translate(30%, 30%) rotate(20deg);
		@include min-lg {
			width: 50%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 50px;
		transform: scaleY(-1) rotate(-65deg);
		@include min-xs {
			width: 70px;
		}
		@include min-md {
			top: 4%;
			right: 8%;
			width: 90px;
		}
		@include min-xl {
			right: 20%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 60px;
			transform: translate(4rem, -3rem) rotate(50deg) scaleY(-1);
			@include min-xs {
				width: 80px;
				transform: translate(8rem, -4rem) rotate(50deg) scaleY(-1);
			}
			@include min-md {
				width: 100px;
				transform: translate(6rem, -3rem) rotate(50deg) scaleY(-1);
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 8%;
		left: 0;
		width: 50px;
		transform: rotate(75deg);
		@include min-xs {
			width: 70px;
		}
		@include min-md {
			left: 5%;
			width: 90px;
		}
		@include min-lg {
			bottom: 13%;
		}
		@include min-xl {
			left: 15%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 60px;
			transform: translate(3rem, -3rem) rotate(-75deg);
			@include min-xs {
				width: 70px;
				transform: translate(5rem, -5rem) rotate(-75deg);
			}
			@include min-md {
				width: 90px;
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		height: 100vh;
		.header {
			position: relative;
			width: fit-content;
			width: 30%;
			height: auto;
			margin: 0 auto;
			padding: 4.5rem 0 4rem;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				color: var(--color-light);
				text-align: center;
				padding: 5rem 2rem;
				@include max-md {
					pad: 4rem 1rem;
				}
				@include max-xs {
					padding: 3rem 1rem;
				}
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border-radius: 50%;
				overflow: hidden;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba($color: #000000, $alpha: 0.3);
					content: "";
				}
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
